.formWrapper {
    background: rgba(17, 17, 17, 0.97);
    color: white;
    padding: 35px 0 30px 0;
}

.formContent {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    text-align: center;
}

.formItemWrapper {
    width: 60%;
    margin-top: 35px;
}
.inputWrapper{
    position: relative;
    margin-bottom: 5px;
}
.inputForm {
    width: 80%;
    height: 25px;
    margin-bottom: 10px;
    padding: 10px;
    border-radius: 7px;
    font-size: 14px;
    outline: none;
    border: none;
    -webkit-box-shadow: inset 0 0 0 50px #fff;
    -webkit-text-fill-color: #000;
}
.text_field_label{
    position: absolute;
    font-size: 14px;
    left: 10%;
    top: -5%;
    opacity: 0.5;
}
.textAreaFrom {
    margin-top: 10px;
    resize: none;
    width: 80%;
    min-height: 100px;
    border-radius: 7px;
    margin-bottom: 10px;
    font-size: 16px;
}

.sendButton {
    font-size: 15px;
    font-weight: 600;
    color: #ffffff;
    background: rgba(17, 17, 17, 0.97);
    border: 1px solid #ffb400;
    border-radius: 20px;
    padding: 10px 15px;
    text-align: center;
    transition: all .25s ease-in-out;
    text-transform: uppercase;
    text-decoration: none !important;
    outline: none !important;
    cursor: pointer !important;
}

.sendButton:hover {
    color: #ffb400;
    border: 1px white solid;
}

@media (max-width: 767px) {
    .formItemWrapper {
        width: 80%;
        margin-top: 35px;
    }
    .inputForm{
        width: 100%;
    }
    .textAreaFrom{
        width: 100%;
    }

}